/*Header y menu-----------------------------------*/
.container_header{
	display: flex;
	position:absolute;
	background: rgb(135,210,255);
	background: linear-gradient(352deg, rgba(135,210,255,0.5410539215686274) 0%, rgba(91,150,187,0.9556197478991597) 39%);
	box-shadow: 0px 4px 6px #9CB6C6;
	width: 100%;
	opacity: 0.85;
	align-items: center;
	flex-direction: column;
	transition: .4s linear ;
	z-index: 21;
	top: 0;
	user-select: none;
	
}
.header-transition{
	display: flex;
	position: absolute;
	width: 100%;
	height: 160px;
	top: 0;
	opacity: 0;
	background:#5B96BB ;
	z-index: 20;
	transition: .4s linear;
}
.imglogo{
	display: flex;
	flex-direction: row;
	position: relative;
	align-items: center;
	width: 200px;
	transition: 0.4s linear;
}
#minilogo{
	z-index: 55;
	position: relative;
	margin-right: -25px;
	transition: 0.3s linear;
	img{
		width: 50%;
	}
}
#logogrande{
	position: relative;
	left: 0;
	z-index: 54;
	transition: opacity 0.2s linear, left .3s linear, scale .3s linear;

}
.nav_header{
	display: flex;
	position: relative;
	align-items: center;
}
.logo-menu{
	display: flex;
	background: linear-gradient(90deg, rgba(79,145,195,1) 0%, rgba(79,145,195,1) 17%, rgba(70,138,186,1) 36%, rgba(55,128,172,1) 57%, rgba(38,116,156,1) 78%, rgba(16,101,136,1) 99%);
	margin: 0 0 1vh 1vh;
	flex-direction: row;
	align-items: center;
	position: relative;
	z-index: 60;
	justify-content: space-between;
}
.menu-resp{
	width: 5vh;
	margin-right: 5vh;
}
.hamburoculto{
	position: relative;
	z-index: 50;
	height: 0;
	top: -500px;
}
.nav a {
	display: flex;
	position: relative;
	font-size: 2vh;
	text-decoration: none;
	background-image: linear-gradient(#93D9E5, #93D9E5);
	background-position: 50% 100%;
	background-repeat: no-repeat;
	background-size: 0% 3px;
	border-radius: 5px;
	font-family: 'Montserrat';
	font-weight: 500;
	transition: font-size .3s,background-size .3s linear;
	&:hover{
		font-size: 2.02vh;
	}
}
.selected{
	background-size: 100% 2px !important;
}
.botones{
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 10px;
	.btn-demo{
		position: relative;
		font-weight: 400;
		font-size: 2vh;
		background: $bg1;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		color: #31358D;
		border-radius: 6px;
		border: none;
		cursor: pointer;
		font-family:'Inter';
		overflow: hidden;
		span {
			position: relative;
			z-index: 10;
			width: 100%;
			transition: color 0.4s;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			padding:1.8vh 0.5vh;
			color: #31358D;
		  }
		  &:hover span{
			color:#fff;
		}
		  &::before,
		  &::after{
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 0;
		  }
		  &::before{
			content: "";
			background: $bg2;
			box-shadow: 0px 13px 20px rgba(0, 0, 0, 0.25);
			width: 120%;
			left: -10%;
			transform: skew(30deg);
			transition: transform 0.4s cubic-bezier(0.3, 1, 0.8, 1);
		  }
		  &:hover::before{
			transform: translate3d(100%, 0, 0);
		  }
		  &:active{
			transform: scale(0.95);
		  }
	}  
	.btn-acceso{
		position: relative;
		font-weight: 400;
		font-size: 2vh;
		background: $bg2;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		color: #31358D;
		border-radius: 6px;
		border: none;
		cursor: pointer;
		font-family:'Inter';
		overflow: hidden;
		span {
			position: relative;
			z-index: 10;
			width: 100%;
			transition: color 0.4s;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			padding:1.8vh 0.5vh;
			color: #fff;
		  }
		  &:hover span{
			color:#31358D;
		  }
		  &::before,
		  &::after{
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 0;
		  }
		  &::before{
			content: "";
			background: $bg1;
			box-shadow: 0px 13px 20px rgba(0, 0, 0, 0.25);
			width: 120%;
			left: -10%;
			transform: skew(30deg);
			transition: transform 0.4s cubic-bezier(0.3, 1, 0.8, 1);
		  }
		  &:hover::before {
			transform: translate3d(100%, 0, 0);
		  }
		  &:active {
			transform: scale(0.95);
		  }
	}
}
