	.contactos-frm{
		display: grid;
		width: 100%;
		height: 700px;
		grid-template-columns: 1fr 1fr;
		.formulariodecontacto{
			display: flex;
			position: relative;
			justify-content: center;
			align-items: center;
			.laoding{
				position: absolute;
				user-select: none;
				pointer-events: none;
				opacity: 0;
				transition: 0.3s;
				.spin{
					border: 3px solid hsla(185, 100%, 62%, 0.2);
					border-top-color: #3cefff;
					border-radius: 50%;
					width: 5em;
					height: 5em;
					animation: spin 1s linear infinite;
				}
				@keyframes spin {
					to {
					  transform: rotate(360deg);
					}
				  }     
			}
		.correcto{
			position: absolute;
			display: flex;
			top: 50px;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			opacity: 0;
			user-select: none;
			pointer-events:none ;
			transition: 0.3s;
			.msjcont{
				display: flex;
				background: #6DA1C2;
				display: flex;
				position: relative;
				flex-direction: row;
				align-items: center;
				padding: 15px;	
				border-radius: 8px;
				p{
				display: flex;
				flex-direction: column;
				// gap: 10px;
				color: #fff;
				font-family: 'Roboto';
				font-weight: 400;
				font-size: 15px;
				}
				img{
					position: relative;
					display: flex;
					width: 35px;
					margin-right: 20px;
				}
			}
		}
		.incorrecto{
			position: absolute;
			display: flex;
			top: 50px;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			opacity: 0;
			user-select: none;
			pointer-events:none ;
			transition: 0.3s;
			p{
				display: flex;
				position: relative;
				flex-direction: row;
				align-items: center;
				color: #fff;
				font-family: 'Roboto';
				font-weight: 400;
				font-size: 15px;
				padding: 15px;
				background: #9F650D;
				border-radius: 8px;
			}
			img{
				position: relative;
				display: flex;
				width: 35px;
				margin-right: 20px;
			}
			button{
				position: relative;
				font-family:Inter;
				top: 30px;
				font-weight: 400;
				font-size: 2.2vh;
				min-width:100%;
				background: $bg1;
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
				color: #31358D;
				border-radius: 6px;
				border: none;
				cursor: pointer;
				overflow: hidden;
				span {
					position: relative;
					z-index: 10;
					width: 100%;
					transition: color 0.4s;
					display: inline-flex;
					align-items: center;
					justify-content: center;
					padding:1.2vh 0.5vh;
					color: #31358D;
				  }
				  &:hover span{
					color:#fff;
				}
				
				  &::before,
				  &::after{
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					z-index: 0;
				  }
				  &::before{
					content: "";
					background: $bg2;
					box-shadow: 0px 13px 20px rgba(0, 0, 0, 0.25);
					width: 120%;
					left: -10%;
					transform: skew(30deg);
					transition: transform 0.4s cubic-bezier(0.3, 1, 0.8, 1);
				  }
				  &:hover::before{
					transform: translate3d(100%, 0, 0);
				  }
				  &:active{
					transform: scale(0.95);
				  }
			} 
		}
		.show{
			opacity: 1;
			transition: 0.3s;
			pointer-events: all;
		}
		form{
			display: flex;
			position:relative;
			flex-direction: column;
			width: 480px;
			gap: 30px;
			transition: 0.3s;
			h3{
				font-family: 'Montserrat';
				font-weight: 500;
				font-size: 2vh;
				color:#4D5A9D ;
			}
			.group-input{
				position: relative;
				input,
				textarea{
					font-family: 'Roboto';
					width: 100%;
					border: none;
					background: transparent;
					// border-radius: 8px;
					// box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
					border-bottom: 1px solid #4D5A9D;
					height: 50px;
					padding: 15px;
					outline: none;
				}
				textarea{
					height: 150px;
					resize: none;
				}
				input:focus + label,
				input:not(:placeholder-shown) + label{
					top: -10px;
					opacity: 0.6;
					transition: 0.3s;
				}
				textarea:focus + label,
				textarea:not(:placeholder-shown) + label{
					top: -10px;
					opacity: 0.6;
					transition: 0.3s;
				}
				label{
					display: flex;
					position: absolute;
					top: 20px;
					left: 20px;
					font-family: 'Roboto';
					font-weight: 400;
					transition: 0.3s;
					color:#4D5A9D ;
					cursor:text;
					user-select: none;
					pointer-events: none;
					img{
						width: 15px;
						margin-right: 10px;
					}
				}
				.error{
					display: flex;
					flex-direction: row;
					position: absolute;
					justify-content: center;
					align-items: center;
					top: 40px;
					user-select: none;
					pointer-events: none;
					opacity: 0;
					transition: all .3s;
					span{
					  width: 20px;
					  height: 20px;
					  text-align: center;
					  color: #fff;
					  background: #9F0D0D;
					  border: 2px solid rgba(159, 13, 13, 0.5);
					  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
					  border-radius: 50%;
					  margin-right: 10px;
					}
					p{
					  font-family: 'Roboto';
					  font-weight: 400;
					  color: #9F0D0D;
					  font-size: 15px;
	  
					}
				  }
				  .msj{
					top: 140px;
				  }
				  .errorShow1{
					opacity: 1;
				  }
				  .errorShow2{
					border-bottom: 2px solid #9f0d0d65;
				  }
			}
			button{
				display: flex;
				position: relative;
				align-items: center;
				font-weight: 400;
				font-size: 2.5vh;
				min-width:162px ;
				height: 50px;
				background: $bg2;
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
				color: #31358D;
				border-radius: 6px;
				border: none;
				cursor: pointer;
				font-family:Inter;
				overflow: hidden;
				span {
					position: relative;
					z-index: 10;
					width: 100%;
					transition: color 0.4s;
					display: inline-flex;
					align-items: center;
					justify-content: center;
					padding:1.8vh 0.5vh;
					color: #fff;
				  }
				  &:hover span{
					color:#31358D;
				  }
				  &::before,
				  &::after{
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					z-index: 0;
				  }
				  &::before{
					content: "";
					background: $bg1;
					box-shadow: 0px 13px 20px rgba(0, 0, 0, 0.25);
					width: 120%;
					left: -10%;
					transform: skew(30deg);
					transition: transform 0.4s cubic-bezier(0.3, 1, 0.8, 1);
				  }
				  &:hover::before {
					transform: translate3d(100%, 0, 0);
				  }
				  &:active {
					transform: scale(0.95);
				  }
			}
		}
	}
		.form-contacto-enviado{
			opacity: 0;
			scale: 0;
			transition: 0.3s ease;
		}
		.tabs-contactos{
			display: flex;
			flex-direction: column;
			justify-content:center;
			align-items: center;
			gap: 50px;
			background: linear-gradient(180deg, rgba(196, 215, 227, 0) 0%, #C4D7E3 62.73%);
			clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, 20% 50%);
			width: 110%;
			margin-left: -10%;
			height: 100%;
			.box-cto{
				display: flex;
				position: relative;
				flex-direction: column;
				width: 250px;
				align-items: left;
				text-align: left;
				span{
					font-family: 'Montserrat';
					color: #4D5A9D;
					font-weight: 500;
					font-size: 2.5vh;
					.correo-icon{
						width: 18px;
					}
					img{
						width: 15px;
						margin-right: 5px;
					}
				}
				p{
					font-family: 'Roboto';
					font-weight: 300;
					font-size: 2vh;
					user-select:text;
				}
				.correo-cto{
					text-decoration: underline;
				}
			}
		}
	}

	.txt-btn{
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 30px;
		.contactos-text{
			font-family: 'Montserrat';
			font-weight: 500;
			font-size: 2.5vh;
			color:#4D5A9D ;
			text-align: center;
			
		}
		button{
			display: flex;
			position: relative;
			align-items: center;
			font-weight: 400;
			font-size: 2vh;
			max-width:400px ;
			height: 50px;
			background: $bg2;
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
			color: #31358D;
			border-radius: 6px;
			border: none;
			cursor: pointer;
			font-family:Inter;
			overflow: hidden;
			span {
				position: relative;
				z-index: 10;
				width: 100%;
				transition: color 0.4s;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				padding:1.8vh 0.5vh;
				color: #fff;
			  }
			  &:hover span{
				color:#31358D;
			  }
			  &::before,
			  &::after{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 0;
			  }
			  &::before{
				content: "";
				background: $bg1;
				box-shadow: 0px 13px 20px rgba(0, 0, 0, 0.25);
				width: 120%;
				left: -10%;
				transform: skew(30deg);
				transition: transform 0.4s cubic-bezier(0.3, 1, 0.8, 1);
			  }
			  &:hover::before {
				transform: translate3d(100%, 0, 0);
			  }
			  &:active {
				transform: scale(0.95);
			  }
		}
	}
}
